<template>
  <div
    class="flex flex-col items-center justify-center bg-slate-300 space-y-10"
    style="height: 100%"
  >
    <div class="flex flex-row">
      <h2>
        {{ Caption }}
      </h2>
      <SoundButton :CaptionText="Caption" LinkSound="" />
      <SoundNativeButton :CaptionText="getNative" LinkSound="" />
    </div>

    <div
      class="card"
      style="height: 500px; width: 350px"
      :style="{ 'background-color': BGColor ? BGColor : 'white', 'background-image':'url('+Img+')' }"
    >
    <!-- <p>IMG:{{ Img }}</p> -->
    <!-- <img :src="Img"/> -->
  </div>
  </div>
</template>

<script lang="ts">
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";
import { defineComponent } from "vue";

import { onMounted, ref } from "vue";

export default defineComponent({
  setup() {
    return {};
  },
  data() {
    return {
      nativeCaption: "",
    };
  },
  // watch: {
  //   Translations() {
  //     console.log("CHANGES");

  //     var locLang = "ru-RU";
  //     var castedArr = <ITranslations[]>this.Translations;
  //     console.log(castedArr);
  //     var foundTransl = castedArr.filter((a) => a.lang == locLang);
  //   },
  //   Caption() {
  //     console.log("Caption");
  //   },
  // },
  computed: {
    getNative() {
      //console.log("getNative");
      var locLang = "ru-RU";
      if (this.Translations) {
        //console.log(this.Translations);
         var castedArr = <ITranslations[]>this.Translations;
        // console.log(castedArr);
         var foundTransl = castedArr.filter((a) => a.lang == locLang);
        // console.log(foundTransl[0].name);

         return foundTransl[0].name;
      } else {
        return "";
      }
    },
  },
  props: {
    Caption: {
      type: String,
      required: true,
    },
    Translations: {
      type: Array,
      required: false,
    },

    BGColor: {
      type: String,
      required: false,
    },
    Img: {
      type: String,
      required: false,
    },
  },
  components: { SoundButton, SoundNativeButton },
});
</script>

<style scoped>
.card {
  border: 2px violet solid;
  /* transform: translateY(-5px); */
  filter: drop-shadow(15px 15px 15px #a809cc);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.card img{
  object-fit: cover;
  object-position: center;
  
}
h2 {
  @apply text-3xl font-bold;
}
</style>
