<template>
  <div>
    <div class="flex flex-row justify-between" style="height: 5vh">
      <BackButton />
      <div class="flex flex-row">
        <h1>Learn the colors</h1>
        <SoundButton CaptionText="Learn colors" LinkSound="" />
        <SoundNativeButton CaptionText="Изучаем цвет&#x301;а" LinkSound="" />
      </div>
      <p></p>
    </div>
    <div class="flex flex-row" style="height: 90vh">
      <div class="basis-4/4">
        <h1></h1>
      </div>
      <div class="basis-1/4 flex content-center justify-center bg-slate-300">
        <SwitchDictButton @click="moveBack">NEXT</SwitchDictButton>
      </div>
      <div class="basis-1/2">
        <CardItem
          :key="currObj.id"
          :Caption="currObj.name"
          :BGColor="currObj.bg"
          :Translations="currObj.translates"
        />
      </div>
      <div class="basis-1/4 flex content-center justify-center bg-slate-300">
        <SwitchDictButton @click="moveNext" :isBack="false"
          >NEXT</SwitchDictButton
        >
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
// import {cDicts} from '@/data/colorsCards.js'
import json from "../data/colorsCards.json";
import CardItem from "./CardItem.vue";
import SwitchDictButton from "@/UI/SwitchDictButton.vue";
import BackButton from "@/UI/BackButton.vue";
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";

export default defineComponent({
  setup() {
    return { json };
  },
  data() {
    return {
      currObj: json[0],
      count: 0,
    };
  },
  components: {
    CardItem,
    SwitchDictButton,
    BackButton,
    SoundButton,
    SoundNativeButton,
  },
  methods: {
    moveBack() {
      if (this.count === 0) {
        this.count = json.length - 1;
      } else {
        this.count = this.count - 1;
      }
      this.currObj = this.json[this.count];
      //console.log(this.currObj.translates);
    },
    moveNext() {
      if (this.count === json.length - 1) {
        this.count = 0;
      } else {
        this.count = this.count + 1;
      }
      this.currObj = this.json[this.count];
    },
  },
});
</script>

<style scoped>
.scene {
  display: flex;
  height: 100vh;
  /* background-color: gray; */
}
</style>
